import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  header: {
    background: 'linear-gradient(to right, rgba(0, 0, 0, .3), rgba(0, 0, 0, .1), rgba(0, 0, 0, .3))'
  },
  viewOnGithub: {
    position: 'fixed',
    top: 0,
    right: 0,
    height: 64,
    zIndex: 1,
    [theme.breakpoints.only('xs')]: {
      height: 56
    }
  }
})

class AppHeader extends React.Component {
  constructor () {
    super()
    this.state = {}
  }

  render () {
    const { classes } = this.props
    return (
      <div className={classes.root}>
        <AppBar position='fixed' className={classes.header}>
          <Toolbar>
            <Typography variant='title' color='inherit'>
              CH08 Parallax Scrolling
            </Typography>
          </Toolbar>
          <a href='https://github.com/sky172839465/the-f2e-ch08-parallax-scrolling' target='block'>
            <img
              className={classes.viewOnGithub}
              src={`https://sky172839465.github.io/the-f2e/img/view-on-github.black.png?now=${Date.now()}`}
              alt='View on github' />
          </a>
        </AppBar>
      </div>
    )
  }
}

AppHeader.propTypes = {}

export default withStyles(styles)(AppHeader)



